/**
 *  Rapier.js
 * @returns {Promise} - ,  ,  Rapier.js 
 */
export async function initRapier() {
    console.log('  Rapier.js...');
    
    // ,    Rapier.js
    if (typeof RAPIER !== 'undefined') {
        console.log('Rapier.js     ');
        return RAPIER;
    }
    
    try {
        //       (CDN)
        // ,   RAPIER  window
        if (typeof window !== 'undefined' && window.RAPIER) {
            console.log(' Rapier.js  window.RAPIER');
            return window.RAPIER;
        }
        
        //   init ,  
        if (typeof RAPIER !== 'undefined' && typeof RAPIER.init === 'function') {
            console.log(' Rapier.js  RAPIER.init()');
            await RAPIER.init();
            return RAPIER;
        }
        
        throw new Error('Rapier.js      ');
    } catch (initError) {
        console.warn('   Rapier.js   :', initError);
        
        //    
        try {
            //  1:    RAPIER    
            console.log('   Rapier.js...');
            
            const waitForRapier = new Promise((resolve, reject) => {
                let attempts = 0;
                const maxAttempts = 10;
                const checkInterval = 300; // ms
                
                const checkRapier = () => {
                    attempts++;
                    if (typeof window.RAPIER !== 'undefined') {
                        console.log(`RAPIER   ${attempts} `);
                        resolve(window.RAPIER);
                        return;
                    }
                    
                    if (attempts >= maxAttempts) {
                        reject(new Error(`RAPIER    ${maxAttempts} `));
                        return;
                    }
                    
                    setTimeout(checkRapier, checkInterval);
                };
                
                checkRapier();
            });
            
            return await waitForRapier;
        } catch (error) {
            console.error('   Rapier.js  :', error);
            
            //      
            return createRapierStub();
        }
    }
}

/**
 *    Rapier.js
 * @returns {Object} -    
 */
function createRapierStub() {
    console.warn('  Rapier.js.     !');
    
    return {
        World: class StubWorld {
            constructor() {
                console.warn('  World   Rapier.js');
                this.bodies = [];
                this.colliders = [];
            }
            
            step() {}
            
            createRigidBody() {
                return {
                    translation: () => ({ x: 0, y: 0, z: 0 }),
                    setTranslation: () => {}
                };
            }
            
            createCollider() {
                return {};
            }
            
            castRay() {
                return null;
            }
            
            castRayAndGetNormal() {
                return { hasHit: false, toi: 0 };
            }
        },
        RigidBodyDesc: {
            fixed: () => ({
                setTranslation: () => ({})
            }),
            dynamic: () => ({
                setTranslation: () => ({}),
                setCanSleep: () => ({}),
                setLinearDamping: () => ({})
            })
        },
        ColliderDesc: {
            cuboid: () => ({}),
            capsule: () => ({})
        },
        Ray: class StubRay {
            constructor(origin, dir) {
                this.origin = origin;
                this.dir = dir;
            }
        }
    };
}

/**
 *   Rapier.js
 * @returns {boolean} -  
 */
export function isRapierAvailable() {
    return typeof RAPIER !== 'undefined' || 
           (typeof window !== 'undefined' && window.RAPIER);
}

/**
 *   Rapier.js
 * @returns {Object|null} -  Rapier.js  null,    
 */
export function getRapier() {
    if (typeof RAPIER !== 'undefined') {
        return RAPIER;
    }
    
    if (typeof window !== 'undefined' && window.RAPIER) {
        return window.RAPIER;
    }
    
    return null;
}