// client/src/core/game.js

//   
import { Renderer } from './renderer.js';
import { InputHandler } from './input.js';
import { NetworkManager } from './network.js';
import { Player } from '../entities/player.js';
import { HUD } from '../ui/hud.js';
import { InventoryUI } from '../ui/inventory.js';
import { PhysicsManager } from '../physics/physics-manager.js';
import { VisionSystem } from '../physics/vision-system.js';

//   
function log(message) {
    if (typeof window !== 'undefined' && window.debugLog) {
        window.debugLog('Game', message);
    } else {
        console.log(`[Game] ${message}`);
    }
}

/**
 *   ,   
 */
class Game {
    constructor() {
        log(' ...');
        
        this.isRunning = false;
        this.isLoading = true;
        this.currentScene = 'loading'; // loading, menu, game
        
        //  DOM
        this.loadingScreen = document.getElementById('loading-screen');
        this.mainMenu = document.getElementById('main-menu');
        this.gameCanvas = document.getElementById('game-canvas');
        this.hudElement = document.getElementById('hud');
        this.inventoryScreen = document.getElementById('inventory-screen');
        this.loadingStatus = document.getElementById('loading-status');
        
        //   
        this.updateLoadingStatus(' ...');
        
        //   
        window.addEventListener('error', this.handleGlobalError.bind(this));
        window.addEventListener('unhandledrejection', this.handlePromiseError.bind(this));
        
        //  
        this.player = null;
        this.entities = [];
        
        //    
        this.gameTime = 0;
        this.physicsUpdateRate = 1/60; // 60    
        this.physicsAccumulator = 0;
        
        //  
        this.fogOfWarEnabled = true;
        this.visibilitySystemEnabled = true;
        
        //   this  
        this.update = this.update.bind(this);
        this.fixedUpdate = this.fixedUpdate.bind(this);
        this.startGame = this.startGame.bind(this);
        
        //      
        this.initModules()
            .then(() => {
                log('  ');
                return this.loadResources();
            })
            .then(() => {
                log(' ');
                this.showMainMenu();
            })
            .catch(error => {
                log(` : ${error.message}`);
                this.showErrorScreen(`   : ${error.message}`);
            });
    }
    
    /**
     *       
     */
    async initModules() {
        try {
            this.updateLoadingStatus(' ...');
            //  
            this.renderer = new Renderer(this.gameCanvas);
            
            this.updateLoadingStatus('  ...');
            //   
            this.input = new InputHandler();
            
            this.updateLoadingStatus('  ...');
            //   
            this.network = new NetworkManager();
            
            this.updateLoadingStatus('  ...');
            //   
            this.physics = new PhysicsManager();
            
            this.updateLoadingStatus(' UI ...');
            //  UI 
            this.ui = {
                hud: this.hudElement ? new HUD(this.hudElement) : null,
                inventory: this.inventoryScreen ? new InventoryUI(this.inventoryScreen) : null
            };
            
            //   
            this.setupEventListeners();
            
            //   
            this.lastFrameTime = 0;
            
            return true;
        } catch (error) {
            log(`  : ${error.message}`);
            throw error;
        }
    }
    
    /**
     *   
     * @param {string} status -  
     */
    updateLoadingStatus(status) {
        if (this.loadingStatus) {
            this.loadingStatus.textContent = status;
        }
        log(status);
    }
    
    /**
     *   
     * @param {ErrorEvent} event -  
     */
    handleGlobalError(event) {
        log(` : ${event.message}  ${event.filename}:${event.lineno}`);
        
        //    ,     
        if (this.currentScene === 'loading') {
            this.showErrorScreen(`: ${event.message}`);
        }
        
        //    
        event.preventDefault();
    }
    
    /**
     *    
     * @param {PromiseRejectionEvent} event -   
     */
    handlePromiseError(event) {
        const errorMessage = event.reason?.message || '  Promise';
        log(`  Promise: ${errorMessage}`);
        
        //    ,     
        if (this.currentScene === 'loading') {
            this.showErrorScreen(`  : ${errorMessage}`);
        }
        
        //    
        event.preventDefault();
    }
    
    /**
     *   
     * @param {string} message -   
     */
    showErrorScreen(message) {
        //   ,     
        let errorScreen = document.getElementById('error-screen');
        if (!errorScreen) {
            errorScreen = document.createElement('div');
            errorScreen.id = 'error-screen';
            errorScreen.className = 'error-overlay';
            errorScreen.innerHTML = `
                <div class="error-container">
                    <h2> </h2>
                    <p id="error-message">${message}</p>
                    <button id="reload-button"></button>
                </div>
            `;
            document.body.appendChild(errorScreen);
            
            //     
            const reloadButton = document.getElementById('reload-button');
            if (reloadButton) {
                reloadButton.addEventListener('click', () => {
                    window.location.reload();
                });
            }
            
            //     
            const style = document.createElement('style');
            style.textContent = `
                .error-overlay {
                    position: fixed;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    background-color: rgba(0, 0, 0, 0.85);
                    z-index: 9999;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }
                .error-container {
                    background-color: #333;
                    padding: 20px;
                    border: 2px solid #f44336;
                    max-width: 80%;
                    text-align: center;
                }
                .error-container h2 {
                    color: #f44336;
                    margin-bottom: 15px;
                }
                .error-container p {
                    color: #fff;
                    margin-bottom: 20px;
                    font-family: monospace;
                    white-space: pre-wrap;
                    text-align: left;
                }
                #reload-button {
                    background-color: #4CAF50;
                    color: white;
                    border: none;
                    padding: 10px 20px;
                    cursor: pointer;
                    font-size: 16px;
                }
                #reload-button:hover {
                    background-color: #45a049;
                }
            `;
            document.head.appendChild(style);
        } else {
            //    
            const errorMessage = document.getElementById('error-message');
            if (errorMessage) {
                errorMessage.textContent = message;
            }
        }
        
        //   
        if (this.loadingScreen) {
            this.loadingScreen.classList.add('hidden');
        }
    }
    
    /**
     *   
     */
    async loadResources() {
        this.updateLoadingStatus('  ...');
        
        try {
            //  
            const physicsInitialized = await this.physics.init();
            if (!physicsInitialized) {
                throw new Error('   ');
            }
            
            this.updateLoadingStatus('  ');
            
            //     
            this.updateLoadingStatus('  ...');
            
            //   (      assets)
            await new Promise((resolve) => {
                setTimeout(() => {
                    this.isLoading = false;
                    resolve();
                }, 1000);
            });
            
            this.updateLoadingStatus('   ');
            return true;
        } catch (error) {
            log(`  : ${error.message}`);
            throw error;
        }
    }
    
    /**
     *   
     */
    setupEventListeners() {
        try {
            //    
            const startGameButton = document.getElementById('start-game');
            if (startGameButton) {
                startGameButton.addEventListener('click', this.startGame);
            }
            
            const settingsButton = document.getElementById('settings');
            if (settingsButton) {
                settingsButton.addEventListener('click', () => {
                    log('');
                    //       
                });
            }
            
            //    
            const closeInventoryButton = document.getElementById('close-inventory');
            if (closeInventoryButton) {
                closeInventoryButton.addEventListener('click', () => {
                    this.hideInventory();
                });
            }
            
            //  
            if (this.input) {
                this.input.addKeyCallback('KeyI', () => {
                    if (this.currentScene === 'game') {
                        this.toggleInventory();
                    }
                });
                
                //  ESC  
                this.input.addKeyCallback('Escape', () => {
                    if (this.currentScene === 'game') {
                        this.togglePause();
                    }
                });
            }
            
            //   VK ( )
            if (window.vkBridge) {
                try {
                    window.vkBridge.send('VKWebAppInit');
                    log('VK Bridge ');
                } catch (e) {
                    log(`  VK Bridge: ${e.message}`);
                }
            }
            
            //    
            window.addEventListener('resize', () => {
                if (this.renderer) {
                    //      
                    this.renderer.render(this.player);
                }
            });
            
            log('  ');
        } catch (error) {
            log(`   : ${error.message}`);
            throw error;
        }
    }
    
    /**
     *   
     */
    showMainMenu() {
        if (this.loadingScreen) {
            this.loadingScreen.classList.add('hidden');
        }
        if (this.mainMenu) {
            this.mainMenu.classList.remove('hidden');
        }
        this.currentScene = 'menu';
        log('  ');
    }
    
    /**
     *  
     */
    startGame() {
        try {
            log(' ...');
            
            if (this.mainMenu) {
                this.mainMenu.classList.add('hidden');
            }
            if (this.gameCanvas) {
                this.gameCanvas.classList.remove('hidden');
            }
            if (this.hudElement) {
                this.hudElement.classList.remove('hidden');
            }
            
            this.isRunning = true;
            this.currentScene = 'game';
            
            //  
            if (this.renderer) {
                this.renderer.setupScene();
                log(' ');
            }
            
            //       
            this.createTestWalls();
            
            //  
            this.player = new Player({
                position: { x: 0, y: 0, z: 0 },
                renderer: this.renderer,
                input: this.input,
                game: this
            });
            
            log(' ');
            
            //   
            this.initVisionSystem();
            
            //    (  )
            this.network.connect().then(() => {
                log('   ()');
            }).catch(error => {
                log(` : ${error.message}`);
            });
            
            //   
            this.lastFrameTime = performance.now();
            requestAnimationFrame(this.update);
            
            log(' ');
        } catch (error) {
            log(`   : ${error.message}`);
            this.showErrorScreen(`   : ${error.message}`);
        }
    }
    
    /**
     *     
     */
    initVisionSystem() {
        try {
            if (!this.renderer || !this.renderer.scene || !this.player) {
                throw new Error('       ');
            }
            
            log('  ...');
            
            //   
            const visionSystem = new VisionSystem({
                scene: this.renderer.scene,
                world: this.physics.getWorld(),
                player: this.player,
                fov: 90, //   
                rayCount: 60, //  
                maxDistance: 50, //   
                memoryEnabled: true, //   
                blurEdges: true //   
            });
            
            //     
            this.renderer.visionSystem = visionSystem;
            
            log('  ');
        } catch (error) {
            log(`   : ${error.message}`);
            throw error;
        }
    }
    
    /**
     *     
     */
    createTestWalls() {
        try {
            if (!this.physics || !this.physics.isReady() || !this.renderer || !this.renderer.scene) {
                log('       ');
                return;
            }
            
            log('  ...');
            
            //    
            const wallMaterial = new THREE.MeshStandardMaterial({
                color: 0x808080,
                roughness: 0.7,
                metalness: 0.2
            });
            
            //    
            const createWall = (x, z, width, depth, height = 3) => {
                //  
                const geometry = new THREE.BoxGeometry(width, height, depth);
                const mesh = new THREE.Mesh(geometry, wallMaterial);
                
                //  
                mesh.position.set(x, height / 2, z);
                
                //   
                this.renderer.scene.add(mesh);
                
                //   
                this.physics.createObstacle({
                    position: { x, y: height / 2, z },
                    size: { x: width, y: height, z: depth },
                    object: mesh
                });
                
                return mesh;
            };
            
            //   
            //   ()
            createWall(0, -20, 40, 1);
            createWall(-20, 0, 1, 40);
            createWall(20, 0, 1, 40);
            createWall(0, 20, 40, 1);
            
            //  
            createWall(-10, -10, 1, 20);
            createWall(10, 10, 20, 1);
            createWall(10, -5, 1, 10);
            createWall(-5, 5, 10, 1);
            
            // 
            createWall(-15, -15, 3, 3);
            createWall(15, -15, 3, 3);
            createWall(15, 15, 3, 3);
            createWall(-15, 15, 3, 3);
            
            log('  ');
        } catch (error) {
            log(`   : ${error.message}`);
        }
    }
    
    /**
     *   
     */
    togglePause() {
        if (this.isRunning) {
            this.pauseGame();
        } else {
            this.resumeGame();
        }
    }
    
    /**
     *   ()
     */
    pauseGame() {
        this.isRunning = false;
        
        if (this.input) {
            this.input.disableGameControls();
        }
        
        //      
        log('  ');
    }
    
    /**
     *  
     */
    resumeGame() {
        this.isRunning = true;
        
        if (this.input) {
            this.input.enableGameControls();
        }
        
        //   
        log(' ');
        
        //   
        this.lastFrameTime = performance.now();
        requestAnimationFrame(this.update);
    }
    
    /**
     *      
     * @param {number} deltaTime -    
     */
    fixedUpdate(deltaTime) {
        try {
            //      (   )
            if (this.network && this.network.isConnected) {
                //    
            }
            
            //    (NPC  .)
            this.entities.forEach(entity => {
                if (entity.fixedUpdate) {
                    entity.fixedUpdate(deltaTime);
                }
            });
        } catch (error) {
            log(`  fixedUpdate: ${error.message}`);
        }
    }
    
    /**
     *   
     * @param {number} timestamp -  
     */
    update(timestamp) {
        if (!this.isRunning) return;
        
        try {
            //      
            const currentTime = performance.now();
            const deltaTime = (currentTime - this.lastFrameTime) / 1000;
            this.lastFrameTime = currentTime;
            
            //  deltaTime    
            const clampedDeltaTime = Math.min(deltaTime, 0.1);
            
            //   
            this.gameTime += clampedDeltaTime;
            
            //     
            this.physicsAccumulator += clampedDeltaTime;
            while (this.physicsAccumulator >= this.physicsUpdateRate) {
                //   
                if (this.physics && this.physics.isReady()) {
                    this.physics.update(this.physicsUpdateRate);
                }
                
                this.fixedUpdate(this.physicsUpdateRate);
                this.physicsAccumulator -= this.physicsUpdateRate;
            }
            
            //  
            if (this.player) {
                this.player.update(clampedDeltaTime);
            }
            
            //   
            this.entities.forEach(entity => {
                if (entity.update) {
                    entity.update(clampedDeltaTime);
                }
            });
            
            //        
            if (this.renderer) {
                this.renderer.render(this.player);
            }
            
            //  UI
            this.updateUI();
            
            //   
            requestAnimationFrame(this.update);
        } catch (error) {
            log(`   : ${error.message}`);
            
            //    ,   
            requestAnimationFrame(this.update);
        }
    }
    
    /**
     *   
     */
    updateUI() {
        //  HUD
        if (this.player && this.ui.hud) {
            this.ui.hud.update({
                health: this.player.health,
                ammo: this.player.ammo,
                weapon: this.player.weapon
            });
        }
    }
    
    /**
     *  
     */
    toggleInventory() {
        if (this.inventoryScreen.classList.contains('hidden')) {
            this.showInventory();
        } else {
            this.hideInventory();
        }
    }
    
    /**
     *  
     */
    showInventory() {
        this.inventoryScreen.classList.remove('hidden');
        
        if (this.ui.inventory) {
            this.ui.inventory.show();
        }
        
        if (this.input) {
            this.input.disableGameControls();
        }
    }
    
    /**
     *  
     */
    hideInventory() {
        this.inventoryScreen.classList.add('hidden');
        
        if (this.ui.inventory) {
            this.ui.inventory.hide();
        }
        
        if (this.input) {
            this.input.enableGameControls();
        }
    }
    
    /**
     *    
     * @param {Object} entity -   
     */
    addEntity(entity) {
        this.entities.push(entity);
    }
    
    /**
     *    
     * @param {Object} entity -   
     */
    removeEntity(entity) {
        const index = this.entities.indexOf(entity);
        if (index !== -1) {
            this.entities.splice(index, 1);
        }
    }
    
    /**
     *  
     */
    endGame() {
        this.isRunning = false;
        this.currentScene = 'menu';
        
        //  
        this.entities = [];
        
        //   
        if (this.renderer && this.renderer.visionSystem) {
            this.renderer.visionSystem.dispose();
            this.renderer.visionSystem = null;
        }
        
        //  
        if (this.physics) {
            this.physics.dispose();
        }
        
        if (this.player) {
            this.player = null;
        }
        
        //   
        if (this.gameCanvas) {
            this.gameCanvas.classList.add('hidden');
        }
        if (this.hudElement) {
            this.hudElement.classList.add('hidden');
        }
        if (this.mainMenu) {
            this.mainMenu.classList.remove('hidden');
        }
        
        log(' ');
    }
}

//      
export { Game };

//     
document.addEventListener('DOMContentLoaded', () => {
    try {
        log('DOMContentLoaded:  ');
        //   
        window.game = new Game();
    } catch (error) {
        console.error('    :', error);
        
        //    
        const errorMessage = document.createElement('div');
        errorMessage.style.cssText = `
            position: fixed;
            top: 0;
            left: 0;
            width: 100%;
            padding: 20px;
            background-color: #f44336;
            color: white;
            text-align: center;
            font-size: 18px;
            z-index: 9999;
        `;
        errorMessage.textContent = ` : ${error.message}`;
        document.body.appendChild(errorMessage);
    }
});